
#navbar1 {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
      justify-content: space-around;
  color: white;
}
#navbar1 .navheader {
    float: left;
    margin: 20px 0;
    font-size: 24px;
}
#navbar1 .navheader .icon {
      fill: #ddd;
      height: 25px;
}
#navbar1 .navlist {
    float: right;
}
#navbar1 .navlist > ul > li {
      display: inline-block;
      margin: 25px 20px;
      font-size: 16px;
}
#navbar1 .navlist > ul > li:hover {
      cursor: pointer;
}



