
.download > button {
  border-radius: 5px;
  margin-bottom: 25px;
  height: 60px;
  width: 250px;
  background: #f1c40f;
  font-size: 20px;
}
.download > button :hover {
    color: white;
}
.download > button > a {
    text-decoration: none;
}
.anno {
  margin: 30px auto;
  width: 1280px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
}
.anno .power {
    font-size: 20px;
}
.anno .cha {
    cursor: pointer;
    color: #3caea2;
    font-size: 20px;
}
.anno .cha > a:hover {
      color: #1d1d1d;
}
.anno .logo {
    position: relative;
    left: -10px;
}
.anno .logo > a {
      padding: 0 18px;
}
.anno .logo > a:hover {
        fill: #3caea2;
}
.anno .logo > a .icon {
        height: 35px;
        width: 35px;
}



